@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

//=========== SLICK SLIDERS ==========//
/* slick sliders */
.slick-list {
	user-select: none; }

// arrows
.slick-arrow, .swiper-arrow {
	@include size(46px, 30px);
	display: block;
	position: absolute;
	margin-top: 0;
	@include font(0, 0);
	border: none;
	border-radius: 0;
	background: none;
	outline: none;
	box-shadow: none;
	opacity: 1;
	z-index: 6;

	@if $has-animation {
		transition: {
			property: background, opacity, visibility;
			duration: 0.2s; } }

	.no-touchevents &:not(.slick-disabled):not(.swiper-button-disabled):hover {
		opacity: 1;
		background: #e6e6e6; }

	&.slick-disabled, .no-touchevents &.slick-disabled,
	&.swiper-button-disabled, .no-touchevents &.swiper-button-disabled {
		opacity: 0.25;
		-ms-filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=25);
		filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=25);
		cursor: default;
		pointer-events: auto;

		&::before {
			opacity: 1; } }

	&::before, &::after {
		@include pseudo();
		@include position(50%);
		border: 0 solid $base-color-alt;
		transform: none;

		@if $has-animation {
			transition: {
				property: border-color;
				duration: $animation-speed; } } }

	&::before {
		@include size(26px, 2px);
		border-top-width: 2px;
		margin: -1px 0 0 -13px; }

	&::after {
		top: 0;
		@include size(12px);
		border-width: 2px 0 0 2px;
		transform: rotate(-45deg);
		margin: 9px 0 0 -13px; }

	.no-touchevents &:not(.slick-disabled):not(.swiper-button-disabled):hover {
		background-color: transparent;

		&::before, &::after {
			border-color: $base-color; } }

	&:not(.slick-disabled):not(.swiper-button-disabled):active {
		margin-top: 1px;
		margin-bottom: -1px; } }

.slick-prev {
	left: 0; }

.slick-next, .swiper-button-next {
	right: 0;

	&::after {
		border-width: 0 2px 2px 0;
		margin: 9px 0 0 1px; } }

// arrows outside
.slick-arrow-outside, .slick-arrow-outside:hover, .slick-arrow-outside:focus {
	background-color: $btn-bg-color; }

// переопределяем стили для ховера и фокуса на стрелки слайдера
.slick-arrow-outside {
	left: auto;
	@include size(30px);

	.no-touchevents &:hover, .no-touchevents &:focus {
		background-color: $btn-bg-hover-color;

		&.slick-disabled {
			background-color: $btn-bg-color; } }

	&::after {
		border-color: white; } }

.slick-prev-outside {
	right: (30px + 10); }

.slick-next-outside {
	right: 0; }

// dots
.slick-dots {
	margin: 0;
	line-height: 0;

	&, .#{$user-content} & {
		margin: 0;
		max-width: none; }

	li {
		vertical-align: top;
		width: auto;
		height: auto;
		margin: 0;

		&, .#{$user-content} & {
			padding: 8px;
			margin: 0 7px;

			&::before {
				content: none; } }

		button {
			@include size(8px);
			padding: 0;
			border-radius: 50%;
			position: relative;
			background-color: transparent;
			border: 1px solid $base-color-alt;

			transition: {
				property: background, border, box-shadow, transform;
				duration: 0.4s; }

			&::before {
				@include pseudo();
				@include position(50%);
				@include size(24px);
				margin: -12px 0 0 -12px;
				border-radius: 50%;
				border: 1px solid $base-color-alt;
				opacity: 0;
				transform: scale(0.2);

				transition: {
					property: opacity, transform;
					duration: 0.2s; } } }

		&.slick-active button {
			background: $base-color-alt;

			&::before {
				opacity: 1;
				transform: scale(1); } } } }

//.no-touchevents &:hover button::before
/* slick sliders end */

// ========== CUSTOM ARROW OF SLIDERS ==========//
/* custom arrow of sliders */
.slider-arrows {
	position: relative;
	display: block;
	text-align: center;
	@include clear-spacing;
	user-select: none; }

.slider-arrows__item {
	position: relative;
	@include display-inline-block-top;
	letter-spacing: normal;
	padding: 0 20px;

	&::before {
		@include pseudo();
		left: -1px;
		top: 50%;
		@include size(0, 22px);
		margin-top: -11px;
		border-right: 2px solid $base-color; }

	&:first-child {
		&::before {
			content: none; } } }

.slider-arr {

	@include size(46px, 30px);
	@include font($font-size, $line-height);
	letter-spacing: normal;
	position: relative;
	//backface-visibility: hidden
	text-indent: -9999px;
	overflow: hidden;
	cursor: pointer;

	@if $has-animation {
		transition: {
			property: background-color;
			duration: $animation-speed; } }

	&::before, &::after {
		@include pseudo();
		@include position(50%);
		border: 0 solid $base-color-alt;
		transform: none;

		@if $has-animation {
			transition: {
				property: border-color;
				duration: $animation-speed; } } }

	&::before {
		@include size(26px, 2px);
		border-top-width: 2px;
		margin: -1px 0 0 -13px; }

	&::after {
		top: 0;
		@include size(12px);
		border-width: 2px 0 0 2px;
		transform: rotate(-45deg);
		margin: 9px 0 0 -13px; }

	.no-touchevents &:not(.slick-disabled):hover {
		background-color: transparent;

		&::before, &::after {
			border-color: $base-color; } }

	&:not(.slick-disabled):active {
		margin-top: 1px;
		margin-bottom: -1px; } }

.slider-arr--next {
	&::after {
		border-width: 0 2px 2px 0;
		margin: 9px 0 0 1px; } }

/* custom arrow of sliders end */

// ======== IMAGES SLIDER =========//
/* images slider */
.images-slider {
	position: relative;
	margin-bottom: $bottom-space--lg;

	@include media('<desk-lg') {
		margin-bottom: $bottom-space--ml; }

	&:last-child {
		margin-bottom: 0; } }

.images-slider__list, .slick-dotted.images-slider__list {
	padding-bottom: $bottom-space;
	margin-bottom: 0;
	position: relative;
	z-index: 2; }

.images-slider__item {
	float: none;
	display: inline-block;
	vertical-align: top;
	opacity: 0;
	white-space: normal;
	overflow: hidden;
	position: relative;
	background: #494949;

	.slick-initialized & {
		display: block;
		float: left;
		opacity: 1; }

	.no-js & {
		display: block;
		width: 100%;
		opacity: 1; }

	&::before {
		content: '';
		display: block;
		height: 0;
		padding-top: 62.5%; }

	img {
		display: block;
		position: absolute;
		@include position(0);
		top: -2px;
		left: 50%;
		//width: 100%
		//height: 100%
		max-width: none;
		text-align: center;
		height: calc(100% + 4px);
		transform: translate(-50%, 0);
		// для ие нельзя исспользовать одновременно lazy-load и object-fit script
 } }		//+object-fit(contain)

.images-slider {

	.slick-arrow {
		right: 0;
		left: auto;
		top: 100%;
		transform: translate(0, 0); }

	.slick-prev {
		right: 40px;

		@include media('<mob-md') {
			right: 0;
			transform: translate(0, 30px); } }

	// slider counter
	.slider-counter {
		position: absolute;
		left: 0;
		top: 100%;
		color: $text-color-light;

		.slide-curr {
			color: $text-color; } }

	.slick-dots, .#{$user-content} & .slick-dots {
		position: absolute;
		bottom: $bottom-space + 1;

		li {
			@include padding-horizontal(7px);

			button {
				width: 6px;
				height: 6px;
				border-radius: 50%;
				background-color: white;
				opacity: 0.5;
				transition: {
					property: opacity, transform;
					duration: $animation-speed; }

				&::before {
					content: none; } }

			&.slick-active button {
				opacity: 1; }

			.no-touchevents &:hover button {
				opacity: 1;
				transform: scale(2); } } } }

/* images slider end */

/* flashes */
.flashes {
	padding: 0 90px;

	@include media('<mob-lg') {
		padding-left: 40px; }

	@include media('<mob-md') {
		padding-right: 50px; } }

.flashes__item {
	@include font(1.4rem, 1.5);
	color: $text-color-light;

	& > *:last-child {
		margin-bottom: 0; } }

/* flashes end */

// ======== PROMO SLIDER =========//
/* promo slider */
.promo {
	position: relative;
	margin-bottom: $bottom-space--lg;
	@include push-auto;

	@include media('<desk-lg') {
		margin-bottom: $bottom-space--ml; }

	&:last-child {
		margin-bottom: 0; }

	.slider-arrows {
		position: absolute;
		bottom: round($promo-slider-space*0.96);
		right: #{$promo-slider-space - 25px};
		z-index: 9999;

		@include media('<desk-hd') {
			bottom: round($promo-slider-space--md*0.96);
			right: #{$promo-slider-space--md - 25px}; }

		@include media('<mob-lg') {
			display: none; } }

	.slick-dots {
		z-index: 9999; } }

.promo-slider {
	// before slide load
	white-space: nowrap;
	overflow: hidden;
	opacity: 0;
	visibility: hidden;
	// before slide load end
	@include push-auto;
	margin-bottom: 0;

	transition: {
		property: opacity, visibility;
		duration: $animation-speed; }

	&.slick-initialized, .no-js & {
		// after slide load
		white-space: normal;
		overflow: visible;
		opacity: 1;
		visibility: visible;
 }		// after slide load end

	@include media('<mob-lg') {
		padding-bottom: 65px; }

	&.slick-dotted {
		margin-bottom: 0; }

	.slick-dots {
		position: absolute;
		width: auto;
		bottom: round($promo-slider-space*0.96);
		left: #{$promo-slider-space - 7};

		@include media('<desk-hd') {
			bottom: $promo-slider-space--md;
			left: #{$promo-slider-space--md - 15}; }

		@include media('<mob-lg') {
			width: 100%;
			left: 0;
			bottom: $grid-space--lg; } } }

.promo-slide {
	// before slide load
	float: none;
	display: inline-block;
	vertical-align: top;
	// before slide load end
	position: relative;
	color: inherit;
	text-decoration: none;
	background-color: $bg-color-base;
	height: 460px;
	perspective: 1500px;
	overflow: hidden;

	.slick-initialized & {
		// after slide load
		float: left;
		display: block;
 }		// after slide load end

	@include media('<desk-hd') {
		height: 380px; }

	@include media('<desk-md') {
		height: 320px; }

	@include media('<mob-lg') {
		height: auto;
		min-height: 240px; } }

.promo-slider__figure {
	overflow: hidden;
	position: absolute;
	@include position(0, 50%);
	@include size(774px, auto);
	max-width: calc(100% - #{$promo-slider-space}*4);
	transform: translate(-50%, -50%);
	border-radius: 50%;

	&::before {
		@include pseudo-block-relative();
		@include ratio(1, 1); }

	@include media('<desk-hd') {
		width: 700px;
		max-width: calc(100% - #{$promo-slider-space--md}*3); }

	@include media('<desk-md') {
		width: 600px;
		max-width: calc(100% - #{$promo-slider-space--md}*2); }

	@include media('<mob-lg') {
		@include position(0, 0);
		@include size(100%);
		max-width: none;
		border-radius: 0;
		transform: none;

		&::before {
			content: none; } } }

.promo-slider__img {
	position: absolute;
	@include position(50%, 0);
	@include size(100%, 50%);
	transform: scale(1.15) translateZ(0);
	//background:
	//osition: center
	//epeat: no-repeat
	//ize: cover

	transition: {
		property: opacity, transform;
		duration: 1.2s; }

	.slick-active & {
		transform: scale(1) translateZ(0); }

	@include media('<mob-lg') {
		top: 0;
		height: 100%;
		background: {
			position: center top; }
		transform: scale(1) translateZ(0); }

	img {
		display: block;
		@include size(100%);
		max-width: none;
		@include object-fit(cover); } }

.promo-slider__text {
	position: absolute;
	@include position(#{round($promo-slider-space*0.96)}, #{round($promo-slider-space + 10px)});
	width: 650px;
	max-width: calc(80% - #{$promo-slider-space}*2);
	@include font-bold;
	@include font(36px, round-float(48/36, 2));
	letter-spacing: 0.05em;
	text-transform: uppercase;
	color: white;
	will-change: transform;
	//opacity: 0
	//transition:
	//roperty: opacity, transform
	//uration: 0.4s
	//transform-origin: 50% top
	//transform: scale(1.1) translate(0, 50px)
	//+antialiased

	//.slick-active &
	///transform: scale(1) translate(0, 0)
	//pacity: 1
	//ransition:
	//duration: 0.8s

	span {
		background: $base-color-alt;
		box-shadow: 10px 0 0 $base-color-alt, -10px 0 0 $base-color-alt; }

	@include media('<desk-hd') {
		@include position(#{$promo-slider-space--md});
		max-width: calc(80% - #{$promo-slider-space--md}*2);
		//+font(30px, 44px)
		font-size: 30px; }

	@include media('<desk-md') {
		//+font(26px, 40px)
		font-size: 26px;
		@include position(auto);
		@include size(auto);
		max-width: none;
		padding: $grid-space--lg;

		span {
			box-shadow: 6px 0 0 $base-color-alt, -6px 0 0 $base-color-alt; } }

	@include media('<mob-lg') {
		position: relative;
		@include font(20px, 32px); } }

/* promo slider end */

// ======== SEVERAL SLIDER =========//
/* several slider */
.several-slider {
	position: relative;
	@include push-auto;
	margin-bottom: $bottom-space--lg;

	@include media('<desk-lg') {
		margin-bottom: $bottom-space--ml; }

	&:last-child {
		margin-bottom: 0; } }

.several-slider-list {
	// before slide load
	white-space: nowrap;
	overflow: hidden;
	opacity: 0;
	visibility: hidden;

	transition: {
		property: opacity, visibility;
		duration: $animation-speed; }
	// before slide load end
	@include push-auto;
	margin-bottom: $bottom-space--md;

	@include media('<desk-lg') {
		margin-bottom: $bottom-space; }

	&:last-child {
		margin-bottom: 0; }

	&.slick-initialized, .no-js & {
		// after slide load
		white-space: normal;
		overflow: visible;
		opacity: 1;
		visibility: visible;
 }		// after slide load end

	&.slick-dotted {
		margin-bottom: 0; } }

.several-slider__item {
	// before slide load
	float: none;
	@include display-inline-block-top;
	// before slide load end
	position: relative;
	color: inherit;
	text-decoration: none;
	background-color: $bg-color-base;

	.slick-initialized & {
		// after slide load
		float: left;
		display: block;
 } }		// after slide load end

.several-slider__inner {
	position: relative;
	overflow: hidden;
	padding: $grid-space;
	color: inherit;
	height: 100%;
	text-decoration: none;
	display: flex;
	justify-content: center;
	flex-direction: column;
	text-align: center;
	z-index: 1;

	//transition:
	//roperty: color
	//uration: $filling-speed-mouseleave

	// ie9
	.ie9 & {
		display: block; }

	&::before {
		@include pseudo();
		right: 0;
		top: 40px;
		bottom: 40px;
		border: 0 solid $border-color {
			width: 0 0 0 2px; }

		transition: {
			property: opacity;
			duration: $filling-speed-mouseleave; } }

	@include media('<mob-md') {
		padding: $grid-space--lg; } }

a.several-slider__inner {
	&::after {
		content: '';
		@include animation-filling(); }

	.no-touchevents &:hover {
		color: $bg-color-base;

		&::after {
			@include animation-filling-active; } }

	// ie9
	.ie9.no-touchevents &:hover {
		background-color: $base-color-alt; } }

.several-slider__item {
	&:last-child {
		.several-slider__inner {
			&::before {
				content: none; } } } }

.no-touchevents .several-slider__item.hover-prev .several-slider__inner,
.no-touchevents a.several-slider__inner:hover {
	&::before {
		opacity: 0;
		transition-duration: $filling-speed; } }

.several-slider__text {
	@include font-normal;
	@include font(16px, round-float(24/16, 2));
	letter-spacing: 0.025em;
	color: inherit;
	padding-bottom: $bottom-space;

	@include media('<mob-md') {
		padding-bottom: 10px; }

	&:last-child {
		padding-bottom: 0; } }

.several-slider__figure {
	margin-bottom: $bottom-space;

	@include media('<mob-md') {
		margin-bottom: 10px; }

	&:last-child {
		margin-bottom: 0; }

	svg {
		@include display-inline-block-top;
		@include size(70px, 50px);

		@include media('<mob-md') {
			@include size(36px); } }

	path {
		fill: $base-color; } }

.several-slider__value {
	@include font-bold;
	@include font(24px, round-float(32/24, 2));
	letter-spacing: 0.05em;
	color: $base-color-alt;
	text-transform: uppercase;
	margin-bottom: 2px;

	.no-touchevents a.several-slider__inner:hover & {
		color: $bg-color-base;
		transition-duration: $filling-speed; }

	&:last-child {
		margin-bottom: 0; } }

.several-slider__unit {
	@include font-bold;
	@include font(14px, round-float(18/14, 2));
	letter-spacing: 0.05em;
	color: $text-color-light;
	//text-transform: uppercase
	margin-bottom: $bottom-space;

	&:last-child {
		margin-bottom: 0; } }

.several-slider__text, .several-slider__value {

	//@if $has-animation
	transition: {
		property: color;
		duration: $filling-speed-mouseleave; }

	.no-touchevents a.several-slider__inner:hover & {
		transition-duration: $filling-speed; } }

/* several slider end */

/* info links slider (for mobile) */
.info-links-slider {
	display: none;

	@include media('<tab-lg') {
		display: block; }

	@include media('<mob-md') {
		.slick-track {
			opacity: 1 !important;
			width: 100% !important;
			transform: none !important; }

		.several-slider__item {
			height: auto !important;
			width: 100% !important;
			border-top: 2px solid $base-color-alt;

			&:first-child {
				border-top: none; }

			&::before {
				display: none; } }

		.slick-cloned {
			display: none !important;

			& + .several-slider__item {
				border-top: none; } }

		.several-slider__inner {
			display: block;
			padding: #{$grid-space--lg} #{round($grid-space--lg*0.6)};
			text-align: left;
			direction: rtl; }

		.slider-arrows {
			display: none; } } }

/* info links slider (for mobile) end */

/*tape slider*/
.tape-slider {
	@include padding-horizontal(50px);
	position: relative;
	margin-bottom: $bottom-space--lg;

	@include media('<desk-lg') {
		margin-bottom: $bottom-space--ml; }

	&:last-child {
		margin-bottom: 0; } }

// swiper slider
.swiper-container {
	position: relative;
	$sw-grad: $bg-color-base;

	&::before, &::after {
		content: '';
		position: absolute;
		top: 0;
		height: 100%;
		width: 15px;
		z-index: 2;
		pointer-events: none; }

	&::before {
		left: 0;
		@include linear-gradient(to right, $sw-grad 0%, transparentize($sw-grad, 0.4) 50%, transparentize($sw-grad, 0.9) 100%); }

	&::after {
		right: 0;
		@include linear-gradient(to left, $sw-grad 0%, transparentize($sw-grad, 0.4) 50%, transparentize($sw-grad, 0.9) 100%); } }

.tape-slider {
	position: relative;
	@include padding-horizontal(round($grid-space--lg*0.5));

	@include media('<desk-lg') {
		@include padding-horizontal(round($grid-space--lg*0.5)); } }

.tape-slider__list {
	&, .#{$user-content} & {
		padding: 0 !important;
		margin: 0 !important;

		li {
			float: left;
			margin: 0;

			.tape-slider__anchor {
				user-select: none;
				padding: 0 #{round($grid-space*0.5)}; }

			@include media('<mob-lg') {
				margin: 0;

				.tape-slider__anchor {
					padding: 0 #{round($grid-space--lg*0.5)}; } } } } }

.tape-slider {
	$tape-arrow-space: 5px;

	.swiper-button-prev,
	.swiper-button-next {
		transform: translate(0, -50%); }

	.swiper-button-prev {
		left: $tape-arrow-space; }

	.swiper-button-next {
		right: $tape-arrow-space; } }


/*tape slider end*/

/*history pages */
.history-pages {
	margin-bottom: $bottom-space--lg;

	@include media('<desk-lg') {
		margin-bottom: $bottom-space--ml; } }

.history-pages-list {
	// before slide load
	white-space: nowrap;
	overflow: hidden;
	opacity: 0;
	visibility: hidden;
	// before slide load end
	margin-right: -15px;

	@include media('<desk-hd') {
		margin-right: 0; }

	&.slick-initialized, .no-js & {
		// after slide load
		white-space: normal;
		overflow: visible;
		opacity: 1;
		visibility: visible;
 } }		// after slide load end

.history-pages__item {
	// before slide load
	float: none;
	display: inline-block;
	vertical-align: top;
	// before slide load end
	padding-right: 15px;

	@include media('<desk-hd') {
		padding-right: 0; }

	.slick-initialized & {
		// after slide load
		float: left;
		display: block;
 } }		// after slide load end

.history-pages__img {
	position: relative;
	margin-bottom: $bottom-space--lg;
	margin-right: -15px;

	@include media('<desk-hd') {
		margin-right: 0; }

	@include media('<desk-lg') {
		margin-bottom: $bottom-space--ml; } }

.history-gallery-open {
	position: absolute;
	right: 0;
	bottom: 0;
	@include size(110px);
	border: 1px solid $base-color;
	border-radius: 50%;
	z-index: 5;
	background: none;
	box-shadow: none;

	@include media('<mob-lg') {
		position: relative;
		display: block;
		right: auto;
		bottom: auto;
		@include push-auto;
		margin-top: 20px;
		@include size(80px); }

	span {
		position: absolute;
		@include position(50%);
		@include size(72px);
		transform: translate(-50%, -50%);
		background: $base-color url(../../img/depict-camera.svg) no-repeat center center;
		background-size: 30px;
		border-radius: 50%;
		@include font(0, 0);

		@include media('<mob-lg') {
			@include size(60px); } }

	// hover
	&:hover {
		border-color: $base-color-alt;

		span {
			background-color: $base-color-alt; } } }

.history-gallery {
	display: none; }

/*history pages end */
