@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/* user options */
.user-options {
	position: relative;
	@include clear-spacing;
	z-index: 100;

	.header & {
		float: right;
		margin-right: -1px; } }

.user-options__item {
	@include display-inline-block-top;
	position: relative;
	@include size(auto, $header-height);
	min-width: $header-height;

	&:first-child ~ & {
		margin-left: -1px; }

	.no-touchevents &:hover {
		z-index: 1; }

	@include media('<tab-lg') {
		height: $header-height--sm;
		min-width: $header-height--sm; }

	& > a {
		display: block;
		overflow: hidden;
		position: relative;
		height: 100%;
		color: $text-color-base;
		// для svg
		fill: $text-color-base;
		text-decoration: none;
		text-align: center;
		border: solid #{$border-color};
		border-width: 0 1px;

		//@if $has-animation
		transition: {
			property: background-color, color, box-shadow;
			duration: $filling-speed-mouseleave; }

		//.no-touchevents &:hover
		//olor: $bg-color-base
		//ill: $bg-color-base
		//order-color: $base-color-alt
		//ackground-color: $base-color-alt
		///box-shadow: -1px 0 0 rgba($bg-color-base, 0.15), inset 0 -1px 0 $border-color

		& > * {
			@include display-inline-block-middle;
			@include font-medium;
			@include font(16px, 20px);
			letter-spacing: 0.05em; }

		&::before {
			@include pseudo-inline-block();
			vertical-align: middle;
			height: 100%; }

		&::after {
			content: "";
			@include animation-filling; }

		svg {
			@include size(24px);
			display: inline-block;

			&.svg-visually-impaired {
				@include size(38px, 21px); }

			&.svg-search {
				@include size(20px); }

			& ~ span {
				padding-left: 15px; } }

		path {
			fill: inherit;

			//@if $has-animation
			transition: {
				property: fill;
				duration: $filling-speed-mouseleave; } }

		& > span {
			display: inline-block;

			&:empty {
				display: none; } }

		.hide-text {
			@include hide-text; }

		// hover and focus
		.no-touchevents &:hover {
			color: $bg-color-base;
			fill: $bg-color-base;
			transition: {
				duration: $filling-speed; }

			&::after {
				@include animation-filling-active; }

			path {
				transition: {
					duration: $filling-speed; } } }

		.ie9 & {
			transition: none;

			path {
				transition: none; }

			&:hover {
				background-color: $base-color-alt; }

			&::after {
				content: none; } } }

	&.width-auto {
		a {
			@include padding-horizontal(25px);

			@include media('<desk-hd') {
				@include padding-horizontal(15px); } } }

	//&.banner-opener
	//isplay: none

	&.cecutient-link {
		width: auto;
		height: $header-height;

		.cecutient-link--norm {
			display: none;

			.cecutient-version & {
				display: inline; } }

		.cecutient-version & .cecutient-link--cec {
			display: none; }

		@include media('<desk-hd') {
			a > span {
				display: none !important; } }

		@include media('<tab-lg') {
			height: $header-height--sm; } }

	&.theme-toggle {
		width: auto;
		min-width: 155px;
		height: $header-height;
		z-index: 2;

		a, .no-touchevents &:active {
			background-color: $base-color-alt;
			color: white;
			fill: white;
			//box-shadow: -1px 0 0 rgba($bg-color-base, 0.15), inset 0 -1px 0 $border-color
			border-width: 0 1px;
			border-color: rgba($border-color, 0.2);
			transition-duration: $animation-speed;

			&::after {
				display: none; }

			.no-touchevents &:hover {
				color: $base-color;
				fill: $base-color;
				border-color: rgba($border-color, 0.2);
				transition-duration: $animation-speed;

				path {
					transition-duration: $animation-speed; } }

			@include media('<tab-lg') {
				span {
					display: none; } }

			@include media('<mob-lg') {
				box-shadow: -1px 0 0 rgba($bg-color-base, 0.15); } }

		svg {
			&.svg-theme--dark {
				@include size(16px); }

			&.svg-theme--light {
				@include size(30px); } }

		path {
			transition-duration: $animation-speed; }

		@include media('<desk-hd') {
			min-width: 130px; }

		@include media('<tab-lg') {
			height: $header-height--sm;
			min-width: $header-height--sm; } }

	.text-theme--light, .svg-theme--light {
		display: none; }

	&.lang {
		width: auto;
		min-width: 120px;

		@include media('<desk-hd') {
			min-width: 100px; }

		@include media('<mob-lg') {
			min-width: 0;
			width: 131px; }

		@include media('<mob-md') {
			min-width: 0;
			width: 60px; } }

	&.cecutient-link, &.map-site-link, &.theme-toggle {
		@include media('<mob-lg') {
			.header & {
				display: none; } } } }

/* user options */
