@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/*FOOTER*/
.footer {

	.layout {
		position: relative; } }

.footer__holder {
	overflow: hidden;
	position: absolute;
	top: auto;
	right: auto;
	bottom: 0;
	cursor: default; }

/*footer gov*/
.footer-gov {
	position: relative; }

.gov {
	z-index: 2; // поднять над контентов (нужно для тени)
	position: relative; }

.gov__list {
	// before slide load
	white-space: nowrap;
	overflow: hidden;
	// before slide load end
	padding: $grid-space calc(103px - #{round($grid-space/2)});

	@include media('<desk-lg') {
		padding: $grid-space--lg calc(103px - #{round($grid-space--lg/2)}); }

	@include media('<tab-lg') {
		padding: $grid-space--lg calc(63px - #{round($grid-space--lg/2)}); }

	&.slick-initialized, .no-js & {
		// after slide load
		white-space: normal;
		overflow: visible;
 }		// after slide load end

	.slick-prev {
		left: 30px;

		@include media('<tab-lg') {
			left: #{round($grid-space--lg/2)}; } }

	.slick-next {
		right: 30px;

		@include media('<tab-lg') {
			right: #{round($grid-space--lg/2)}; } } }
.gov__item {
	// before slide load
	float: none;
	@include display-inline-block-top;
	// before slide load end
	padding: 0 #{round($grid-space/2)};

	@if $has-animation {
		transition: {
			property: background-color;
			duration: $animation-speed; } }

	.slick-initialized & {
		// after slide load
		float: left;
		display: block;
 } }		// after slide load end

.gov__inner {
	display: block;
	color: $text-color;
	text-decoration: none;
	padding: 20px;
	border: 1px solid $border-color;

	@if $has-animation {
		transition: {
			property: background-color, border-color;
			duration: $animation-speed; } }

	.no-touchevents &:hover {
		background-color: $base-color;
		border-color: $base-color; }

	p {
		margin-bottom: round($bottom-space/2);

		&:last-child {
			margin-bottom: 0; } } }

.gov__figure {
	position: relative;
	margin: 0 auto #{round($bottom-space/2)};
	max-width: 320px;

	&:before {
		@include pseudo-block-relative();
		@include ratio(14, 5); }

	&:last-child {
		margin-bottom: 0; } }

.gov__img {
	position: relative;
	overflow: hidden;
	@include cover-abs();
	backface-visibility: hidden;

	img {
		display: block;
		max-height: 100%;
		max-width: 100%;
		transform: translate(-50%, -50%);
		position: absolute;
		@include position(50%); } }

/*footer gov end*/

/*footer common*/
.footer-common {
	position: relative;
	color: inherit;

	a {
		color: $link-color;
		text-decoration: none;
		cursor: pointer !important;

		@if $has-animation {
			transition: {
				property: color;
				duration: $animation-speed; } }

		.no-touchevents &:hover {
			color: $link-color; } }

	.dl {
		list-style: none;

		li {
			margin-bottom: 0;
			white-space: nowrap;

			@include media('<desk-hd') {
				white-space: normal; } }

		.dt, .dd {
			@include display-inline-block-top;
			white-space: normal; }

		.hide {
			display: none; } }

	.adr {
		@include font-normal; } }

// footer nav
.footer-nav {
	@include grid-parent($grid-space);
	@include clear-spacing;

	@include media('<desk-lg') {
		@include grid-parent($grid-space--lg); } }

.footer-nav__col {
	position: relative;
	@include grid-item(4, $grid-space);
	@include font(16px, round-float(24/16, 2));
	letter-spacing: 0.025em;

	&:nth-last-child(1) {
		text-align: right; }

	@include media('<desk-lg') {
		@include grid-item-responsive(2, $grid-space--lg);

		&:nth-last-child(3) {
			text-align: right; } }

	@include media('<mob-lg') {
		@include grid-item-responsive(1, $grid-space--lg);
		text-align: center !important; }

	& > *:last-child {
		margin-bottom: 0; } }

.footer-nav-list {
	list-style: none;
	padding: 0;
	margin: 0;

	li {
		margin-bottom: 20px;
		overflow: hidden;

		&:last-child {
			margin-bottom: 0; } } }

.footer-nav__item {
	margin-bottom: 20px;

	&:last-child {
		margin-bottom: 0; } }

/*footer common end*/

/*developer*/
.developer {
	display: inline-block;

	a {
		text-decoration: none;

		.no-touchevents &:hover {
			text-decoration: none; } } }

.developer__logo {
	@include display-inline-block-top;
	position: relative;
	top: 5px;
	padding-right: 3px;
	@include size(154px, 20px);
	text-align: left;
	text-indent: -9999px;

	&::before, &::after {
		@include pseudo();
		@include position(0, 0);
		@include size(149px, 16px);

		@if $has-animation {
			transition: {
				property: opacity, transform, filter;
				duration: 0.2s; } } }

	&::before {
		@include pseudo();
		@include position(4px, 4px);
		background: url('data:image/png;base64,');
		background-size: 2px; }

	//.no-touchevents a:hover &
	///transform: translate(0, 1px)
	///filter: blur(2px)
	///opacity: 0.6

	&::after {
		@include pseudo();
		background: #00ef85 url('data:image/png;base64,') no-repeat center center;
		background-size: auto 16px;

		.no-touchevents a:hover & {
			transform: translate(0, -1px); } } }

/*developer end*/
/*FOOTER end*/
