@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/* navigation */
$nav-drop-item-width: 320px;
$nav-drop-item-width--sm: 240px;

.nav-wrap {
	.header & {
		float: left;

		@include media('<desk-sm') {
			display: none; } }

	.sidebar & {
		display: none;

		@include media('<desk-sm') {
			display: block; } } }

.nav {
	position: relative; }

.nav-list {
	@include margin-horizontal(-#{round($nav-item-space*0.5)});
	@include clear;

	@include media('<desk-hd') {
		@include margin-horizontal(-#{round($nav-item-space--sm*0.5)}); }

	li {
		position: relative;

		&.hover > .nav__tab a {
			background-color: $base-color-alt;
			color: $bg-color-base; } }

	a {
		display: block;
		@include padding-horizontal(#{round($nav-item-space*0.5)});
		@include font(16px, 20px);
		text-decoration: none;
		color: $text-color-base;
		@if $has-animation {
			transition: {
				property: background-color, color;
				duration: $animation-speed; } }

		@include media('<desk-hd') {
			@include padding-horizontal(#{round($nav-item-space--sm*0.5)}); } }

	.nav__angle {
		display: none; } }

.nav__tab {
	& .short-text {
		display: none !important; }

	@include media('<desk-lg') {
		.header & .full-text {
			display: none !important; }

		.header & .short-text {
			display: inline-block !important; } } }

// первый уровень навигации
.nav-list {
	& > li {
		float: left;

		& > .nav__tab {
			a {
				height: $header-height;
				@include font-medium;
				@include clear-spacing;

				&::before {
					@include pseudo-inline-block();
					height: 100%;
					vertical-align: middle; }

				span {
					@include display-inline-block-middle;
					@include font($font-size, $line-height);
					letter-spacing: 0.05em; } } }

		// selected
		&.selected > .nav__tab {
			a {
				color: $base-color; } }

		// hover
		&.hover > .nav__tab {
			a {
				color: $bg-color-base; } } } }

.nav__drop {
	position: absolute;
	left: 0;
	top: 100%;
	background-color: $base-color-alt;
	border: 1px solid $border-color-light;
	margin-top: -1px;
	width: round($nav-drop-item-width*2);
	opacity: 0;
	visibility: hidden;
	z-index: 10;
	@include clear-spacing;
	overflow: hidden;

	@include media('<desk-sm') {
		display: none; }

	li.hover > & {
		opacity: 1;
		visibility: visible; }

	li {
		@include display-inline-block-top;
		position: relative;
		width: calc(100%/2);
		letter-spacing: 0.025em;

		&::before, &::after {
			@include pseudo();
			border: 0 solid $border-color-light; }

		&::before {
			@include position(100%, 0);
			@include size(9999px, 0);
			border-top-width: 1px; }

		&::after {
			@include position(0, 100%);
			@include size(0, 9999px);
			border-right-width: 1px; } }

	.nav__tab {
		height: 100%; }

	a {
		padding: round($nav-item-space*0.5) round($nav-item-space*0.5);
		color: $bg-color-base;
		height: 100%; }

	li.hover > .nav__tab a {
		background-color: $base-color;
		color: $bg-color-base; }

	.nav__drop {
		top: 0;
		left: 100%; } }

/* navigation overlay */
.overlay-menu {
	@include media('>=desk-sm') {
		opacity: 0 !important;
		visibility: hidden !important;
		transition: {
			property: opacity, visibility;
			duration: $animation-speed; } } }

/* navigation overlay end */
/* navigation end */
