@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/* lg */
.lg-backdrop {
	z-index: 9998;
	background-color: $bg-color-base; }

.lg-outer {
	z-index: 9999; }

#lg-zoom-in, #lg-zoom-out, #lg-actual-size {
	display: none;

	.zoom-img-popup & {
			display: block; } }

.lg-sub-html, .lg-toolbar {
	background: rgba($bg-color-base, 0.5);
	border: 0 solid $border-color; }

.lg-sub-html {
	border-width: 1px 0 0;
	padding: 27px 40px;
	@include font-normal;
	color: $text-color;
	@include font(16px, round-float(24/16, 2));

	& > * {
		@include truncate(100%); } }

.lg-toolbar {
	border-width: 0 0 1px;

	.lg-icon {
		position: relative;
		@include size($header-height);
		border: solid $border-color;
		border-width: 0 1px;
		margin-left: -1px;
		transition: none;
		color: $text-color;

		&::after {
			position: absolute;
			@include position(50%);
			transform: translate(-50%, -50%); }

		&:hover {
			background-color: $base-color-alt; }

		@include media('<tab-lg') {
			@include size($header-height--sm); } } }

.lg-close {
	padding: 0;

	&::before {
		@include pseudo-inline-block();
		vertical-align: middle;
		height: 100%; }

	&::after {
		@include display-inline-block-middle;
		vertical-align: middle; }

	.no-touchevents &:hover {
		background-color: $base-color-alt; } }

#lg-counter {
	padding-left: 40px;
	padding-top: 27px;
	@include font-medium;
	@include font(16px, round-float(24/16, 2));
	color: $text-color-light;

	@include media('<tab-lg') {
		padding-top: 20px;
		padding-left: 20px; } }

#lg-counter-current {
	color: $text-color; }

.lg-actions {
	.lg-next, .lg-prev {
		background: rgba($bg-color-base, 0.5);
		border: 2px solid $base-color-alt;
		border-radius: 0;
		@include size(60px);

		@if not $has-animation {
			transition: none; }

		&::before, &::after {
			color: $base-color-alt; }

		.no-touchevents &:hover {
			background-color: $base-color-alt;

			&::before, &::after {
				color: $bg-color-base; } } }

	.touchevents & {
		display: none; } }

.zoom-img-popup.lg-grab img.lg-object {
	cursor: zoom-in; }

.zoom-img-popup.lg-zoomed img.lg-object {
	cursor: zoom-out; }

.zoom-img-popup.lg-grabbing img.lg-object {
	cursor: move;
	cursor: grabbing; }

/* lg end */
