@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/* info links */
.info-links {
	background: {
		image: url(../../img/bg-logo-right-bottom.png);
		position: right bottom;
		repeat: no-repeat;
		size: 89.1%; }

	.aside & {
		margin-bottom: $aside-widget-bottom-space;

		@include media('<desk-lg') {
			margin-bottom: $layout-spaces--lg; }

		@include media('<tab-lg') {
			display: none; }

		&:last-child {
			margin-bottom: 0; } } }

.info-links__inner {
	display: block;
	overflow: hidden;
	position: relative;
	color: $base-color-alt;
	text-decoration: none;
	text-align: center;
	padding: 30px 40px;
	background-color: $bg-color-base;
	z-index: 2;

	&::after {
		content: '';
		@include animation-filling; }

	@include media('<desk-lg') {
		padding: #{round($grid-space--lg*0.9)} #{$grid-space--lg}; }

	@include media('<mob-md') {
		display: block;
		padding: #{$grid-space--lg};
		text-align: left;
		direction: rtl; }

	&::before {
		@include pseudo();
		@include position(0, 50%);
		@include size(220px, 0);
		margin-left: -110px;
		border-top: 2px solid $border-color;

		will-change: opacity;
		transition: {
			property: opacity;
			duration: $filling-speed-mouseleave; }

		@include media('<desk-lg') {
			width: 186px;
			margin-left: round(-186px/2); } }

	.info-links__item:first-child & {
		&::before {
			content: none; } } }

a.info-links__inner {
	.no-touchevents &:hover, .current & {
		color: $bg-color-base;

		&::after {
			@include animation-filling-active; }

		.info-links__text {
			transition: {
				duration: $filling-speed; } } }

	.hover-next &, &:hover, .current &, .current + .info-links__item & {
		&::before {
			opacity: 0;
			transition-duration: $filling-speed; } }

	// ie9
	.ie9 & {
		&::before {
			transition: none !important; } }

	.ie9 &:hover,
	.ie9 .current & {
		background-color: $base-color-alt; } }

.info-links__text {
	display: block;
	@include font-medium;
	@include font(16px, round-float(19/16, 2));
	letter-spacing: 0.05em;
	text-transform: uppercase;
	margin-bottom: 10px;

	transition: {
		property: color;
		duration: $filling-speed-mouseleave; }

	.ie9 & {
		transition: none; }

	@include media('<desk-lg') {
		@include font(15px, round-float(19/15, 2)); }

	@include media('<mob-md') {
		@include display-inline-block-middle;
		margin: 0 0 10px;
		width: calc(100% - 36px - 5px);
		padding-left: $grid-space--lg;
		direction: ltr;

		a & {
			margin-bottom: 0; } }

	&:last-child {
		margin-bottom: 0; } }

.info-links__icon {
	margin-bottom: 10px;

	svg {
		@include display-inline-block-top;
		@include size(28px);

		a & {
			@include size(50px); } }

	path {
		fill: $base-color; }

	@include media('<mob-md') {
		@include display-inline-block-middle;
		@include size(36px);

		svg {

			&, a & {
				@include size(36px); } } }

	&:last-child {
		margin-bottom: 0; } }

.info-links__links {
	&, .#{$user-content} & {
		margin: 0 -15px;
		padding: 0;
		list-style: none;

		@include media('<mob-md') {
			@include margin-horizontal(-5px); }

		li {
			padding-left: 0;
			margin: 0 5px 5px;

			&::before {
				content: none; } } }

	& {
		margin-bottom: 10px;
		@include clear-spacing;

		&:last-child {
			margin-bottom: 0; }

		li {
			@include display-inline-block-top; }

		a {
			padding: 4px 11px;
			font-size: 14px;
			text-transform: none; } } }

/* info links end */

/* menu aside */
.menu-aside {
	display: none;
	margin-bottom: $bottom-space--ml;

	@include media('<tab-lg') {
		display: block; }

	.aside & {
		display: block;
		margin-bottom: $aside-widget-bottom-space;

		@include media('<desk-lg') {
			margin-bottom: $layout-spaces--lg; }

		@include media('<tab-lg') {
			display: none; }

		&:last-child {
			margin-bottom: 0; } }

	.info-links__inner {

		@include media('<mob-md') {
			padding: #{$grid-space--sm};
			text-align: center;
			direction: ltr; } }

	.info-links__text {

		@include media('<mob-md') {
			width: 100%;
			padding-left: 0; } } }

/* menu aside end */
