@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/* submenu */
.submenu {
	position: relative;
	padding-top: 15px;

	.sidebar & {
		@include padding-horizontal($sidebar-inside-space-alt);

		@include media('<desk-lg') {
			@include padding-horizontal($sidebar-inside-space-alt--lg); } } }

.submenu-list {
	position: relative;
	border: 1px solid $base-color;
	padding: round($grid-space*0.85) $grid-space;

	@include triangle-pretty(top, (right: 50%, bottom: 100%), $bg-color-base, 14px, 22px, $base-color, 1px);

	@include media('<desk-lg') {
		padding: round($grid-space--lg*0.6) $grid-space--lg; }

	li {
		position: relative; }

	a {
		display: block;
		@include padding-vertical(8px);
		@include font(16px, round-float(24/16, 2));
		letter-spacing: normal;
		text-decoration: none;
		color: $text-color-base;

		@include media('<desk-lg') {
			line-height: round-float(20/16, 2); }

		@if $has-animation {
			transition: {
				property: background-color, color;
				duration: $animation-speed; } } }

	// контейнер с ссылкой и стрелкой
	.submenu__tab {
		position: relative; }

	// стрелка
	.submenu__angle {
		display: none;
		position: absolute;
		right: 0;
		top: 50%;
		@include size(16px, 10px);
		transform: translate(0, -50%);
		line-height: 0;
		pointer-events: none;

		& > i {
			$this-icon-lines-width: 10px;
			$this-icon-lines-height: 2px;
			$this-icon-lines-color: $text-color-base;

			display: block;
			position: relative;
			@include size(100%);

			&::before, &::after {
				@include pseudo();
				right: 0;
				top: 50%;
				backface-visibility: hidden;
				@include size($this-icon-lines-width, $this-icon-lines-height);
				margin-top: -1px;
				background-color: $this-icon-lines-color;

				@if $has-animation {
					transition: {
						property: background-color;
						duration: $filling-speed-mouseleave; } } }

			&::before {
				transform: rotate(-45deg); }

			&::after {
				right: round(get-catheter($this-icon-lines-width) - $this-icon-lines-height*0.5);
				transform: rotate(45deg); } } }

	// ховер общий
	a:hover {
		color: $base-color;

		& + .submenu__angle {
			& > i {
				&::before, &::after {
					background-color: $base-color; } } } }

	// пункт с открытым дропом
	li.open {
		& > .submenu__tab {
			a {
				color: $base-color; }

			.submenu__angle {
				& > i {
					transform: rotate(0.5turn);

					&::before, &::after {
						background-color: $base-color; } } } } }

	// текущий пункт
	li.current {
		& > .submenu__tab {
			a {
				color: $bg-color-base;
				background-color: $base-color; }

			.submenu__angle {
				& > i {

					&::before, &::after {
						background-color: $bg-color-base; } } } } }

	// если есть дроп
	li.has-drop {
		& > .submenu__tab {
			a {
				padding-right: 20px; }

			.submenu__angle {
				display: block; } } } }

// первый уровень навигации
.submenu-list {
	& > li {
		& > .submenu__tab {
			a {
				@include font-medium; } } }

	& > li.current {
		& > .submenu__tab {
			a {
				color: $base-color;
				background: none; }

			.submenu__angle {
				& > i {

					&::before, &::after {
						background-color: $base-color; } } } } } }

.submenu__drop {
	display: none;

	li.open > & {
		display: block; }

	a {
		@include padding-horizontal(20px); }

	.submenu__angle {
		display: none !important; }

	// текущий пункт (второй уровень)
	//li.current
	// > .submenu__tab
	//a
	//	color: $bg-color-base
	//	background-color: $base-color

	.submenu__drop {
		top: 0;
		left: 100%;

		a {
			font-size: 14px;
			@include padding-horizontal(30px); } } }

