@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/* user form */
.form-title {
	display: block;
	@include font-bold;
	@include font($font-size-h3, $line-height-h3);
	color: $title-color;
	text-align: center;
	margin-bottom: $bottom-space--md;

	@include media('<tab-sm') {
		@include font($font-size-h3--sm, $line-height-h3--sm);
		margin-bottom: $bottom-space; } }

.user-form {
	padding: 70px 40px;
	margin-bottom: $bottom-space--lg;

	@include media('<desk-lg') {
		margin-bottom: $bottom-space--ml; }

	@include media('<tab-lg') {
		padding: 40px; }

	@include media('<mob-lg') {
		padding: 20px; }

	&:last-child {
		margin-bottom: 0; }

	.layout-text {
		max-width: 700px; }

	form {
		display: block; }

	.form-row, .input-wrap {
		margin-bottom: 40px;

		.input-wrap {
			margin-bottom: 0; } }

	.input-wrap, .form-row .input-wrap {
		@include media('<mob-lg') {
			margin-bottom: 40px; } }

	textarea {
		height: 130px;
		max-height: 130px;
		resize: none; }

	.captcha {
		@include display-inline-block-top;
		@include clear-spacing;
		position: relative; // Важно! Для позицирования отметки подтверждения полей формы
		z-index: 1; // Важно! Отметка подтверждения полей формы имеет z-index: -1

		@include clear;

		img {
			$margin-right: 20px;
			display: inline-block;
			vertical-align: middle;
			max-width: calc(50%);
			margin-right: $margin-right;

			& + input {
				display: inline-block;
				vertical-align: middle;
				width: 200px; } }

		@include media('<tab-lg') {
			img {
				display: block;
				max-width: none;
				margin-bottom: $bottom-space--sm;

				& + input {
					display: block;
					max-width: none;
					width: 100%; } } } }

	.form-footer {
		@include clear;

		@include media('<mob-lg') {
			padding-top: $bottom-space--sm; } }

	.form-buttons {
		float: right;

		@include media('<mob-lg') {
			float: none;
			margin: 0; }

		.btn-submit {
			width: 310px;

			@include media('<mob-lg') {
				width: auto; } } }

	.form-note {
		overflow: hidden;
		@include clear-spacing;

		&::before {
			content: '';
			display: inline-block;
			vertical-align: middle;
			min-height: inherit; } }

	.form-note__item {
		display: inline-block;
		vertical-align: middle;
		width: calc(100% - 6px);
		@include font($font-size--sm, $line-height--sm);
		letter-spacing: normal; } }

/* user form end */
