@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

.aside__holder {
	position: relative; }

/* aside widget */
.aside-widget, .banners-item {
	margin-bottom: $layout-spaces;

	@include media('<desk-hd') {
		margin-bottom: $layout-spaces--hd; }

	@include media('<desk-lg') {
		margin-bottom: $layout-spaces--lg; }

	&:last-child {
		margin-bottom: 0; } }

.aside-widget__title {
	@include font(36px, round-float(42/36, 2));
	text-transform: uppercase;
	color: $title-color;
	margin-bottom: 30px;

	@include media('<desk-lg') {
		font-size: 28px;
		margin-bottom: $bottom-space; } }

/* aside widget end */

/* actual links */
.actual-links {
	list-style: none;
	padding: 0;
	margin: 0;
	@include font-light;
	@include font(1.6rem, round-float(24/16, 2));

	li {
		margin-top: $bottom-space;

		@include media('<desk-lg') {
			margin-top: $bottom-space--sm; }

		&:first-child {
			margin-top: 0; } }

	a {
		color: inherit;
		text-decoration: none;
		padding-top: 0.3em;
		transition: {
			property: color;
			duration: $animation-speed; }

		span {
			border-bottom: 1px solid tint($text-color, 40%);
			transition: {
				property: border-color;
				duration: $animation-speed; } }

		.no-touchevents &:hover {
			color: $base-color;

			span {
				border-color: tint($base-color, 40%); } } } }

/* actual links end */

/* banners */
.banners {
	@include media('<tab-lg') {
		@include grid-parent($layout-spaces--lg);
		@include clear-spacing; } }

.banners-item {
	@include media('<tab-lg') {
		@include grid-item(2, $layout-spaces--lg);
		letter-spacing: 0.025em; }

	@include media('<mob-md') {
		@include grid-item-responsive(1, $layout-spaces--lg); } }

/* banners end */

/* banner inner */
.banner-inner {
	display: block;
	padding: 40px 48px 48px;
	text-align: center;
	@include font(16px, round-float(24/16, 2));
	color: inherit;

	@include media('<desk-lg') {
		padding: #{round($grid-space--lg*0.9)} #{$grid-space--lg} #{$grid-space--lg}; } }

.banner-inner__title {
	@include font-medium;
	@include font(16px, round-float(24/16, 2));
	text-transform: uppercase;
	color: $base-color-alt;
	margin: 0 -15px 1em;

	@include media('<desk-lg') {
		@include margin-horizontal(-5px); } }

.banner-inner__figure {
	margin-bottom: 1em;

	svg {
		@include display-inline-block-top;
		@include size(50px); }

	path {
		fill: $base-color; } }

.banner-inner__content {
	text-align: left;
	margin-bottom: 1em; }

.banner-inner__footer {
	[class*="btn-"] {
		display: block; } }

/* banner inner end */

/* banner */
.banner {
	display: block;
	text-decoration: none;
	color: $text-color;

	img {
		width: 100%; } }

/* banner end */
