@import "../utils/_mixins";

// =============================================================================
// Google Fonts
// =============================================================================
@import url('https://fonts.googleapis.com/css?family=Roboto+Slab:400,700|Roboto:400,400i,700,700i&subset=cyrillic');

// =============================================================================
// Font Face
// =============================================================================
// BrutalType
@include font-face("brutaltype", "../fonts/brutal-type/regular/brutaltype", normal, normal, eot woff ttf svg);
@include font-face("brutaltype-bold", "../fonts/brutal-type/bold/brutaltype-bold", normal, normal, eot woff ttf svg);
@include font-face("brutaltype-extrabold", "../fonts/brutal-type/extrabold/brutaltype-extrabold", normal, normal, eot woff ttf svg);
@include font-face("brutaltype-light", "../fonts/brutal-type/light/brutaltype-light", normal, normal, eot woff ttf svg);
@include font-face("brutaltype-medium", "../fonts/brutal-type/medium/brutaltype-medium", normal, normal, eot woff ttf svg);

// MenuetScript
@include font-face("menuet-script", "../fonts/menuet-script/regular/menuet_script", normal, normal, eot woff ttf svg);
