@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/* main scheme */
.main-scheme {
	margin-bottom: $bottom-space--lg;

	@include media('<desk-lg') {
		margin-bottom: $bottom-space--ml; }

	&:last-child {
		margin-bottom: 0; } }

.main-scheme-visual {
	margin-bottom: $bottom-space--md;

	@include media('<desk-lg') {
		margin-bottom: $bottom-space--ml; }

	@include media('<tab-lg') {
		margin-bottom: $bottom-space; }

	&:last-child {
		margin-bottom: 0; }

	img {
		display: block;
		@include push-auto; }

	.is-theme-dark {
		display: none; }

	.theme-dark & {
		.is-theme-light {
			display: none; }

		.is-theme-dark {
			display: block; } }

	.theme-dark.cecutient-version & {
		.is-theme-light {
			display: block; }

		.is-theme-dark {
			display: none; } } }

.main-scheme-description {
	@include clear;
	@include clear-spacing;
	@include grid-parent($grid-space);

	ul {
		@include grid-item(4, $grid-space);
		@include padding-vertical(0);
		@include font-normal();
		@include font(16px, round-float(24/16, 2));
		letter-spacing: 0.025em;
		margin: 0;

		@include media('<desk-hd') {
			@include grid-item-responsive(2, $grid-space); }

		@include media('<mob-md') {
			@include grid-item-responsive(1, $grid-space); }

		li {
			margin: 12px 0 0;
			padding: 0;
			@include clear;

			&::before {
				content: none; }

			&:first-child {
				margin-top: 0; } }

		li > span {
			display: block;
			overflow: hidden; } }

	.ico {
		float: left;
		margin-right: 14px;
		line-height: 0;
		white-space: normal;
		@include clear-spacing;

		&::before {
			content: "";
			@include display-inline-block-middle;
			height: 24px; }

		img {
			@include display-inline-block-middle;
			font-size: $font-size;
			letter-spacing: normal; }

		.is-theme-dark {
			display: none; }

		.theme-dark & {
			.is-theme-light {
				display: none; }

			.is-theme-dark {
				display: inline-block; } }

		.theme-dark.cecutient-version & {
			.is-theme-light {
				display: inline-block; }

			.is-theme-dark {
				display: none; } } } }

/* main scheme end */
