@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

html {
	font-size: 62.5%;

	&.css-scroll-fixed {
		@include scroll-blocked(); } } // блокировка скролла

body {
	@include font-normal;
	@include font($font-size, $line-height);
	letter-spacing: 0.025em;
	color: $text-color;
	fill: $text-color; // для svg иконок

	*::selection {
		background-color: $base-color;
		color: white; } }

h1, h2, h3, h4, h5, h6 {
	padding: 0;
	@include font-medium;
	color: $title-color;
	margin: 0 0 $bottom-space;

	&:last-child {
		margin-bottom: 0; }

	@include media('<mob-lg') {
		margin-bottom: round-float($bottom-space*0.85, 1);

		&:last-child {
			margin-bottom: 0; } }

	a {
		text-decoration: none;
		color: inherit;

		.no-touchevents &:hover {
			color: $base-color;
			text-decoration: underline; } } }

h1, .heading-article h2 {
	@include font-extra-bold;
	@include font($font-size-h1, $line-height-h1);
	margin-bottom: $bottom-space--lg;

	@include media('<desk-hd') {
		@include font($font-size-h1--lg, $line-height-h1--lg); }

	@include media('<desk-lg') {
		margin-bottom: $bottom-space--ml; }

	@include media('<desk-md') {
		@include font($font-size-h1--md, $line-height-h1--md); }

	@include media('<mob-lg') {
		@include font($font-size-h1--sm, $line-height-h1--sm); } }

h2 {
	@include font($font-size-h2, $line-height-h2);

	@include media('<desk-md') {
		@include font($font-size-h2--md, $line-height-h2--md); }

	@include media('<mob-lg') {
		@include font($font-size-h2--sm, $line-height-h2--sm); } }

h3 {
	@include font($font-size-h3, $line-height-h3);

	@include media('<desk-md') {
		@include font($font-size-h3--md, $line-height-h3--md); }

	@include media('<mob-lg') {
		@include font($font-size-h3--sm, $line-height-h3--sm); } }

h4 {
	@include font($font-size-h4, $line-height-h4);

	@include media('<desk-md') {
		@include font($font-size-h4--md, $line-height-h4--md); }

	@include media('<mob-lg') {
		@include font($font-size-h4--sm, $line-height-h4--sm); } }

h5 {
	@include font($font-size-h5, $line-height-h5); }

h6 {
	@include font($font-size-h6, $line-height-h6);
	text-transform: uppercase; }

p {
	padding: 0;
	margin: 0 0 $bottom-space;

	&:last-child {
		margin-bottom: 0; }

	&:empty {
		display: none; } }

hr {
	line-height: 1px;
	height: 0;
	border: none;
	background: none;
	border-bottom: 1px solid $border-color;
	margin-bottom: $bottom-space; }

a {
	color: $link-color;
	text-decoration: underline;
	cursor: default;
	outline: none;

	&:link {
		cursor: pointer; }

	.no-touchevents &:hover {
		text-decoration: none; }

	&[href^="tel"], .no-touchevents &[href^="tel"]:hover {
		text-decoration: none !important;
		color: inherit;
		cursor: default; }

	&[href^="javascript:"] {
		cursor: default; } }

img {
	border: none;
	vertical-align: top;
	max-width: 100%;

	table & {
		max-width: none; } }

b, strong, th {
	@include font-bold; }

i, em {
	@include font-italic; }

i b, i strong, em b, em strong, b i, b em, strong i, strong em {
	@include font-bold-italic; }

/* add shadow */
.add-shadow, .add-shadow--full {
	//visibility: visible
	position: relative;
	z-index: auto;

	&::after {
		@include pseudo();
		@include size(100%);
		@include position(15px);
		z-index: -1;
		background: url(../../img/bg-shadow.png) right bottom;

		@include media('<desk-lg') {
			@include position(10px); } } }

.add-shadow--full {
	background-color: $bg-color-base;
	border: 2px solid $base-color-alt;

	&::after {
		@include position(17px);

		@include media('<desk-lg') {
			@include position(12px); } } }

/* add shadow end */

/* heading */
.heading-article {
	position: relative;
	min-height: $datetime-size;
	margin-bottom: $bottom-space--lg;

	@include media('<desk-lg') {
		margin-bottom: $bottom-space--ml; }

	&:last-child {
		margin-bottom: 0; } }

/* heading end */

/* datetime */
.datetime {
	display: block;
	position: absolute;
	@include position(6px, 0);
	@include size($datetime-size);
	padding: 8px 5px 0;
	margin-bottom: $bottom-space;
	@include font-medium;
	@include font(15px, round-float(24/15, 2));
	letter-spacing: normal;
	text-align: center;
	text-transform: uppercase;
	color: $base-color-alt;

	@include media('<mob-lg') {
		position: relative;
		padding: 0;
		@include size(auto);
		@include font($font-size, $line-height);
		letter-spacing: $letter-spacing;
		text-transform: uppercase;
		color: $text-color-light;
		border: none;
		background-color: transparent;
		text-align: left;

		&::after {
			content: none; } }

	&:last-child {
		margin-bottom: 0; }

	& > strong {
		display: block;
		@include font-bold;
		@include font(60px, 1);
		margin-bottom: -5px;

		@include media('<mob-lg') {
			@include display-inline-block-top;
			@include font(inherit, inherit);
			margin-bottom: 0; } } }

/* datetime end */

/* date simple */
.date-simple {
	display: block;
	margin-bottom: $bottom-space;
	@include font-medium;
	font-size: 16px;
	letter-spacing: 0.05em;
	text-transform: uppercase;
	color: $text-color-light; }

/* date simple end */

