@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/* popular */
.popular {
	margin-bottom: $bottom-space--lg;
	background-color: $bg-color-base;

	@include media('<desk-lg') {
		margin-bottom: $bottom-space--ml; }

	&:last-child {
		margin-bottom: 0; } }

.popular__inner {
	position: relative;
	vertical-align: middle;
	color: $base-color-alt;
	text-decoration: none;
	text-align: center;
	padding: #{round($grid-space*1.5)} #{$grid-space};
	display: flex;
	justify-content: space-between;
	flex-direction: column;
	z-index: 1;

	@if $has-animation {
		transition: {
			property: background-color, color;
			duration: $animation-speed; } }

	@include media('<desk-lg') {
		padding: #{round($grid-space--lg*1.5)} #{$grid-space--lg}; }

	@include media('<mob-md') {
		padding: #{round($grid-space--sm*1.5)} #{$grid-space--sm}; }

	//+media('<mob-md')
	//isplay: block
	//adding: #{$grid-space--lg}
	//ext-align: left
 }	//irection: rtl

.popular__title {
	@include font-normal;
	@include font(16px, round-float(24/16, 2));
	letter-spacing: 0.025em;
	color: inherit;
	padding-bottom: $bottom-space;

	//+media('<mob-md')
	//adding-bottom: 10px

	&:last-child {
		padding-bottom: 0; } }

.popular__text {
	display: block;
	@include font-medium;
	@include font(16px, round-float(19/16, 2));
	letter-spacing: 0.05em;
	text-transform: uppercase;
	margin-bottom: 20px;

	@include media('<desk-hd') {
		font-size: 14px;
		letter-spacing: 0.025em; }

	//+media('<mob-md')
	//include display-inline-block-middle
	//argin: 0
	//idth: calc(100% - 36px - 5px)
	//adding-left: $grid-space--lg
 }	//irection: ltr

.popular__icon {
	margin-bottom: $bottom-space;

	//+media('<mob-md')
	//argin-bottom: 10px

	&:last-child {
		margin-bottom: 0; }

	svg {
		@include display-inline-block-top;
		@include size(50px); }

	path {
		fill: $base-color; }

	//+media('<mob-md')
	//include display-inline-block-middle
	//include size(36px)

	//vg
 }	//@include size(36px)

.popular__value {
	@include font-bold;
	@include font(24px, round-float(32/24, 2));
	letter-spacing: 0.05em;
	color: inherit;
	text-transform: uppercase;
	margin-bottom: 2px;

	.no-touchevents .several-slider__inner:hover & {
		color: $bg-color-base;
		transition-duration: $filling-speed; }

	&:last-child {
		margin-bottom: 0; } }

// hover
.no-touchevents a.popular__inner:hover {
	background-color: $base-color-alt;
	color: $bg-color-base; }

/* popular end */
