@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/* content head */
.content-head {
	position: relative;
	background-color: $base-color-alt;
	background-position: center;
	background-size: cover;
	color: white;

	&::before {
		@include pseudo();
		@include position(0);
		@include size(100%);
		@include linear-gradient(to right, transparentize($base-color-alt, 0.5) 0%, transparentize($base-color-alt, 1) 100%); }

	& > * {
		position: relative;
		z-index: 2; }

	h1, h2, h3 {
		color: white;
		text-transform: uppercase;
		margin-right: $aside-width;
		line-height: 1.3;

		@include media('<desk-hd') {
			margin-right: $aside-width--hd; }

		@include media('<desk-lg') {
			margin-right: $aside-width--lg; }

		@include media('<tab-lg') {
			margin-right: 0; } }

	.breadcrumbs__list a {
		color: white; } }

/* content head end */
