@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/* photo gallery */
.ph-gallery {
	margin-bottom: $bottom-space--lg;

	@include media('<desk-lg') {
		margin-bottom: $bottom-space--ml; } }

.ph-gallery__list {
	@include grid-parent(40px);
	@include clear-spacing;

	@include media('<desk-lg') {
		@include grid-parent($grid-space--lg); } }

.ph-gallery__item {
	@include grid-item(3, 40px);
	letter-spacing: 0.025em;

	@include media('<desk-lg') {
		@include grid-item-responsive(3, $grid-space--lg); }

	@include media('<mob-lg') {
		@include grid-item-responsive(2, $grid-space--lg); }

	@include media('<mob-md') {
		@include grid-item-responsive(2, $grid-space--lg); } }

.ph-gallery__inner {
	text-decoration: none;
	display: block;
	user-select: none; }

.ph-gallery__figure {
	position: relative; }

.ph-gallery__img {
	position: relative;
	overflow: hidden;
	margin: 0 0 3.8rem;
	font-size: $font-size;
	letter-spacing: normal;
	background-color: $border-color;

	&:last-child {
		margin-bottom: 0; }

	&::before {
		@include pseudo-block-relative();
		@include ratio(225, 170); }

	&::after {
		@include pseudo();
		@include position();
		@include size(100%);
		background-color: transparentize($base-color-alt, 0.5);
		opacity: 0;
		visibility: hidden;
		transition: {
			property: opacity, visibility;
			duration: 0.4s;
			timing-function: cubic-bezier(0.65, 0.05, 0.36, 1); } }

	img {
		display: block;
		width: 100%;
		height: 100%;
		position: absolute;
		@include position(0);
		@include object-fit(cover);
		will-change: transform;
		//backface-visibility: hidden
		transition: {
			property: transform;
			duration: 0.5s; } }

	.ph-gallery-all {
		@include display-inline-block-top;
		letter-spacing: 0.025em; } }

.ph-gallery__zoom {
	will-change: transform, opacity;
	position: absolute;
	@include position(50%);
	z-index: 5;
	opacity: 0;
	transform: translate(-50%, -50%) scale3d(1.2, 1.2, 1);
	//backface-visibility: hidden
	transition: {
		property: transform, opacity;
		duration: 0.5s; }

	// ie9
	.ie9 & {
		transform: translate(-50%, -50%); }

	.touchevents & {
		@include size(24px);
		left: auto;
		right: 10px;
		top: 10px;
		transform: none;
		opacity: 1;
		filter: drop-shadow(1px 1px 1px black); } }

.no-touchevents a.ph-gallery__inner:hover {

	.ph-gallery__img {

		&::after {
			opacity: 1;
			visibility: visible; }

		img {
			transform: scale3d(1.1, 1.1, 1); } }

	.ph-gallery__zoom {
		opacity: 1;
		transform: translate(-50%, -50%) scale3d(1, 1, 1); } }

/* photo gallery end */

/* photo gallery preview */
.ph-gallery-preview {
	margin-bottom: $bottom-space--lg;

	@include media('<desk-lg') {
		margin-bottom: $bottom-space--ml; } }

.ph-gallery-preview__list {
	@include grid-parent(40px);
	@include clear-spacing;

	@include media('<desk-lg') {
		@include grid-parent($grid-space--lg); } }

.ph-gallery-preview__item {
	@include grid-item(4, 40px);
	letter-spacing: 0.025em;

	@include media('<desk-lg') {
		@include grid-item-responsive(4, $grid-space--lg); }

	@include media('<desk-md') {
		@include grid-item-responsive(2, $grid-space--lg); }

	@include media('<mob-md') {
		@include grid-item-responsive(1, $grid-space--lg); } }

.ph-gallery-preview__inner {
	text-decoration: none; }

.ph-gallery-all {
	display: block;
	@include size(225px, 170px);
	overflow: hidden;
	position: relative;
	text-decoration: none;
	color: $bg-color-base;
	text-align: center;
	background-color: $base-color-alt;
	cursor: pointer;
	z-index: 1;

	.ph-gallery__figure & {
		@include size(auto); }

	//.ph-gallery__figure &
	//osition: absolute
	//include size(100%)
	//include position(0)

	&::after {
		@include pseudo-content();
		@include animation-filling($base-color); }

	&::before {
		@include pseudo-block-relative();
		@include ratio(225, 170); }

	//&::before
	//include pseudo-content()
	//include display-inline-block-middle
	//eight: 100%

	.no-touchevents &:hover {
		&::after {
			@include animation-filling-active; } } }

.ph-gallery-all__holder {
	//+display-inline-block-middle
	//width: calc(100% - 6px)
	position: absolute;
	width: 100%;
	@include position(50%, 0);
	padding: 0 10px;
	transform: translate(0, -50%);

	& > i {
		margin-bottom: 20px;

		&:last-child {
			margin-bottom: 0; } }

	& > span {
		display: block;
		margin-bottom: 20px;
		@include font-medium;
		font-size: 16px;
		text-transform: uppercase;

		&:last-child {
			margin-bottom: 0; } } }

/* photo gallery preview end */
