@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/*files links*/
.file {
	$file-icon-size: 42px;
	@include display-inline-block-top;
	min-height: $file-icon-size;
	padding: 0.5em;
	@include font(16px, 20px);
	color: $base-color-alt;
	text-decoration: none;

	@if $has-animation {
		transition: {
			property: background-color;
			duration: $animation-speed; } }

	@include clear;

	svg {
		float: left;
		@include size($file-icon-size); }

	path {
		fill: $base-color-alt;

		@if $has-animation {
			transition: {
				property: fill;
				duration: $animation-speed; } } }

	& > span, & > em {
		display: block;
		margin-left: #{$file-icon-size};
		padding-left: 1em;

		@if $has-animation {
			transition: {
				property: color;
				duration: $animation-speed; } } }

	& > em {
		display: block;
		font-size: 1.4rem;
		color: $text-color-light; }

	.no-touchevents &:hover {
		background-color: $base-color;

		& > em {
			color: $bg-color-base; } } }

/*files links end*/
