@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/* google lang */
#google_translate_element {
	height: 100%;

	.skiptranslate {
		height: 100%; } }

.goog-te-gadget-simple {
	display: block !important;
	height: 100%;
	letter-spacing: $letter-spacing;
	border: solid $border-color !important;
	border-width: 0 0 1px 1px !important;
	padding: 0 10px !important;

	&::before {
		@include pseudo-inline-block();
		vertical-align: middle;
		height: 100%; }

	@include media('<mob-md') {
		font-size: 7pt !important;
		padding: 6px 2px 0 !important;

		&::before {
			content: none; } }

	img {
		@include display-inline-block-middle;
		//filter: grayscale(1)

		@include media('<mob-md') {
			display: block;
			margin: 0 auto 4px; }

		& + span {
			@include display-inline-block-middle;
			width: 75px;

			@include media('<mob-md') {
				display: block;
				width: auto; } } }

	a {
		@include display-inline-block-middle;
		padding: 0 !important;
		color: $text-color;

		img {
			display: none; }

		span {
			display: none; }

		& > span:first-child {
			@include display-inline-block-middle;
			@include font-medium;
			letter-spacing: normal;
			line-height: 1.2;
			white-space: normal;
			text-transform: uppercase;
			text-align: center;
			max-width: 65px;
			word-wrap: break-word; } }

	&:hover {
		background-color: $base-color-alt;
		border-color: $base-color-alt !important;

		a {
			color: $bg-color-base; } } }

.goog-te-menu-frame {
	box-shadow: none !important;
	background-color: $bg-color-base !important;
	border: 1px solid $border-color !important; }

/* google lang end */
