@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

.contacts-page {
	h1 {
		text-transform: uppercase; }

	.layout-article {
		min-height: calc(100vh - #{$header-height} - #{$layout-spaces});
		padding-right: 51%;
		@include clear;

		@include media('<desk-lg') {
			min-height: calc(100vh - #{$header-height} - #{$layout-spaces--lg}); }

		@include media('<desk-md') {
			padding-right: 0; } }

	.aside {
		display: none; }

	.main__holder {
		@include media('<desk-md') {
			margin-right: 0; } } }

/* contacts info */
.info-container {
	float: left;
	padding-bottom: 40px;

	@include media('<desk-md') {
		float: none; } }

.info-container__list {
	@include clear-spacing;
	@include clear;

	@include media('<desk-md') {
		@include grid-parent($layout-spaces--lg); } }

.info-container__item {
	@include font($font-size, $line-height);
	letter-spacing: 0.025em;
	color: inherit;
	margin-bottom: $bottom-space--md;

	&:last-child {
		margin-bottom: 0; }

	.short-contacts li:first-child {
		padding-top: 0; }

	.short-contacts-wrap {
		text-align: left; }

	@include media('<desk-md') {
		@include grid-item(2, $layout-spaces--lg); }

	@include media('<mob-lg') {
		@include grid-item(1, $layout-spaces--lg); } }

.info-container__title {
	@include font-medium;
	@include font(16px, 24px);
	letter-spacing: 0.05em;
	text-transform: uppercase;
	color: $text-color-base;
	margin-bottom: $bottom-space; }

.info-container__text {
	margin-bottom: $bottom-space;

	&:last-child {
		margin-bottom: 0; } }

/* contacts info end */

/* contacts map */
.contacts-map {
	position: absolute;
	top: 0;
	right: 0;
	left: 50%;
	height: calc(100vh - #{$header-height});
	//width: calc((100vw - #{$sidebar-width} - #{$aside-width})/2 + #{$aside-width})

	@include media('<desk-lg') {
		height: calc(100vh - #{$header-height--sm}); }

	@include media('<desk-md') {
		height: 320px;
		position: relative;
		top: auto;
		left: auto;
		right: auto; } }

.contacts-map__frame {
	height: 100%;
	background: $bg-color-selected; }

/* contacts map end */

/* map popup */
.map-popup__title {
	display: block;
	@include font-bold;
	@include font(20px, 24px);
	text-transform: uppercase;
	margin-bottom: 0.8em;
	color: $title-color; }

.map-popup__row {
	@include font(14px, 18px);
	padding: 0 0 1em;
	@include clear;

	sup, sub {
		font-size: 0.6em; }

	[class*="depict"] {
		@include size(18px);
		float: left;
		margin-right: 1rem;

		& + div {
			overflow: hidden; } } }

/* map popup end */
